.navDark, .navLight {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-weight: 600;
  font-size: 1rem;
  width: 50%;
  padding-top: 1rem;

  .brand {
    font-size: 1.1rem;
    font-weight: 800;
  }

  .signIn {
    color: lighten(mediumpurple, 10%);
  }

  button {
    text-transform: lowercase;
    background-color: mediumpurple;
    padding: .5rem 1rem;
    font-size: 1rem;
    border: none;
    border-radius: 100px;
    color: #f3f3f3;
    box-shadow: 0 0 .5rem rgba(0, 0, 0, 0.5);
  }
}

.navDark {
  color: #8f8f8f;
  transition: all 600ms ease;

  .brand {
    color: #f3f3f3;
    transition: all 600ms ease;
  }
}

.navLight {
  color: #4f4f4f;
  transition: all 600ms ease;

  .brand {
    color: #181818;
    transition: all 600ms ease;
  }
}