@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,300;0,400;0,600;0,800;1,100;1,300;1,400;1,600;1,800&display=swap');

body {
  overflow: hidden;
}

.light, .dark {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 100vh;
  font-family: 'Raleway', sans-serif;
  footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
    padding: 1rem 0;
  }

  .toggler {
    font-size: 1.5rem;
    position: absolute;
    top: 1rem;
    right: 1.5rem;
    &:hover {
      cursor: pointer;
    }
  }
}
.dark {
  background-color: #181818;
  color: #f3f3f3;
  transition: all 600ms ease;
}

.light {
  background-color: #f3f3f3;
  color: #181818;
  transition: all 600ms ease;
}
