.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .hero {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.5rem;
    img {
      width: 25%;
    }
    .heroText {
      width: 30%;
      h1 {
        font-size: 4rem;
        .purple {
          color: lighten(mediumpurple, 15%);
        }
      }
      button {
        margin-top: 1rem;
        text-transform: lowercase;
        background-color: mediumpurple;
        padding: .5rem 1rem;
        font-size: 1.2rem;
        border: none;
        border-radius: 100px;
        color: #f3f3f3;
        box-shadow: 0 0 .5rem rgba(0, 0, 0, 0.5);
        &:last-of-type {
          margin-left: 1rem;
          background-color: #f3f3f3;
          color: #181818;
        }
      }
    }
  }
}